import React from 'react'
import PropTypes from "prop-types"
import { Link } from "gatsby"

const Course = ({title}) => (
    <div className="course pt-4 pb-4 pl-4 pr-4 mb-5">
        <div className="price">Free Course</div>
        <h2>{title}</h2>
        <p>This course is for beginner developers who want to start working with Magento 2. We cover all the basics so you can get up to speed with Magento 2 platform.</p>
        <Link className="btn btn-secondary" to="#" role="button">Start Your Journey</Link>
    </div>
)

Course.propTypes = {
    title: PropTypes.string,
    level: PropTypes.string,
}

Course.defaultProps = {
    title: `Magento 2 Course`,
    level: `Beginner`
}

export default Course
